let id = ""
let timer;
let heightArr=[]
export default {
	data() {
		return {
			content: "",
			richTextHeight: 0,
			isShowMore: false,
			articleHeight: 0
		}
	},
	onLoad(options) {
		id = options.id
		this.init()
		setTimeout(() => {
			this.richTextHeight = Math.max.apply(null,heightArr)
			clearInterval(timer)
		}, 5000)
	},
	async onReady() {
		timer = setInterval(async () => {
			var size = await this.getElSize('fake-rich')
			heightArr.push(size.height)
			console.log(Math.max.apply(null,heightArr))
			if (Math.max.apply(null,heightArr) >= this.screenHeight * 2) {
				this.richTextHeight = this.screenHeight * 2
				this.isShowMore = true
			} else {
				this.richTextHeight = Math.max.apply(null,heightArr) 
				this.isShowMore = false
			}
		}, 100)
	},
	onUnload() {
		clearInterval(timer)
	},
	methods: {
		init() {
			this.api.home.article.get_detail({
				article_id: id,
				request_type: "app"
			}, res => {
				console.log(res.data)
				this.content = res.data.content
				
			})
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true,
					rect: true
				}, (data) => {
					res(data);
				}).exec();
			})
		},
		showAll() {
			clearInterval(timer)
			this.richTextHeight = Math.max.apply(null,heightArr) 
			this.isShowMore = false
		}
	}
}
